import './styles.scss';
import React, {Component} from 'react';
import { connect } from 'react-redux';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import bem from 'bem-cn';
import Header from '../../components/header';
import ProgressBar from '../../components/progress-bar';
import Options from '../../components/steps/options';
import SubOptions1 from '../../components/steps/sub-options-1';
import SubOptions2 from '../../components/steps/sub-options-2';
import CarSelectionStep from '../../components/steps/car-selection-step';
import CarModelSelectionStep from '../../components/steps/car-model-selection-step';
import CarEngineSelectionStep from '../../components/steps/car-engine-selection-step';
import CarRegistrationDateInputStep from '../../components/steps/car-registration-date-input-step';
import AgeInputStep from '../../components/steps/age-input-step';
import AdditionalDriversAgeSelectionStep from '../../components/steps/additional-drivers-age-selection-step';
import PostalCodeInputStep from '../../components/steps/postal-code-input-step';
import InsuranceSelectionStep from '../../components/steps/insurance-selection-step';
import KaskoInsuranceSelectionStep from '../../components/steps/kasko-insurance-selection-step';
import OfferFinalStep from '../../components/steps/offer-final-step';

const b = bem('layout');

class App extends Component {
    renderPage = () => {
        const { currentStepIndex, flows, currentFlow } = this.props.state;
        const currentStep = currentFlow ? flows[currentFlow].steps[currentStepIndex] : 'options';
        switch (currentStep) {
            case 'options':
                return (<Options key={1}/>);
            case 'sub-options-1':
                return (<SubOptions1 key={2}/>);
            case 'sub-options-2':
                return (<SubOptions2 key={3}/>);
            case 'car-selection':
                return <CarSelectionStep key={5}/>;
            case 'car-model-selection':
                return <CarModelSelectionStep key={6}/>;
            case 'car-engine-selection':
                return <CarEngineSelectionStep key={7}/>;
            case 'car-registration-date-input':
                return <CarRegistrationDateInputStep key={16}/>;
            case 'age-input-step':
                return <AgeInputStep key={17}/>;
            case 'additional-drivers-age-selection':
                return <AdditionalDriversAgeSelectionStep key={11}/>;
            case 'postal-code-input':
                return <PostalCodeInputStep key={12}/>;
            case 'insurance-selection':
                return <InsuranceSelectionStep key={13}/>;
            case 'kasko-insurance-selection':
                return <KaskoInsuranceSelectionStep key={14}/>;
            case 'offer-final-step':
                return <OfferFinalStep key={15}/>;
            default:
                return (<div key={10}>Wrong step</div>)
        }
    };

    render() {
        return(
            <div className="container">
                <Header/>
                <ProgressBar/>
                <ReactCSSTransitionGroup
                    className="wizard"
                    transitionName="page"
                    transitionEnterTimeout={500}
                    transitionLeaveTimeout={500}>
                    {this.renderPage()}
                </ReactCSSTransitionGroup>
            </div>
        )
    }
}

const mapState = state => {
    return {state}
}

export default connect(mapState,null)(App);



